<template>
  <ion-page>
    <Back
      @click="() => (checkStatus = false)"
      :btnSrc="
        () => {
          if (checkStatus === false) router.push('/tabs/main');
        }
      "
    />
    <Layout
      height="false"
      outlineBtn="."
      :method="() => (checkStatus = false)"
      :filledBtn="'.'"
      :title="'Обращение'"
    >
      <template v-slot:main-content>
        <div v-if="appealsListResponse?.data">
          <ion-list v-for="el in appealsListResponse?.data" :key="el.title">
            <div
              class="item"
              @click="
                () => router.push({ name: 'appealsMessages', params: el })
              "
            >
              <div class="item-header">
                <ion-text class="title ion-text-start">№{{ el.id }}</ion-text>
                <ion-badge>
                  {{ el.messages }}
                </ion-badge>
              </div>
              <ion-item>
                <ion-text class="text time">{{ el.date_create }}</ion-text>
              </ion-item>
            </div>
          </ion-list>
        </div>
        <div v-else class="loading">
          <ion-spinner name="bubbles"></ion-spinner>
        </div>
      </template>

      <template v-slot:content>
        <LayoutBox>
          <template v-slot:content>
            <div
              @click="
                () =>
                  router.push({
                    name: 'appealsMessages',
                    params: { newAppeal: true },
                  })
              "
            >
              <ion-text class="sub-title ion-text-start"> Обращение </ion-text>
              <ion-item>
                <ion-img
                  slot="start"
                  class="icon-start"
                  :src="require('@/assets/img/send.png')"
                ></ion-img>
                <!-- <ion-icon class="icon-start" slot="start" :icon="paperPlaneOutline"></ion-icon> -->
                <ion-text class="sub-title">Создать новое обращение</ion-text>
                <ion-icon
                  class="icon-end"
                  slot="end"
                  :icon="chevronForwardOutline"
                ></ion-icon>
              </ion-item>
            </div>
          </template>
        </LayoutBox>
      </template>
    </Layout>
  </ion-page>
</template>


<script>
import { defineComponent } from "vue";
import { useRouter } from "vue-router";
import Layout from "../components/Layout.vue";
import LayoutBox from "../components/LayoutBox.vue";
import Back from "../components/Back.vue";
import { paperPlaneOutline, chevronForwardOutline } from "ionicons/icons";
import {
  IonPage,
  IonText,
  IonItem,
  IonList,
  IonBadge,
  IonSpinner,
  IonIcon,
  IonImg,
  onIonViewDidEnter,
} from "@ionic/vue";
import { useAppealsStore } from "../stores/appeals";
import { storeToRefs } from "pinia";
import { Storage } from "@ionic/storage";

export default defineComponent({
  name: "appealsPage",
  components: {
    IonPage,
    Layout,
    IonText,
    IonItem,
    IonList,
    Back,
    IonBadge,
    IonIcon,
    IonImg,
    IonSpinner,
    LayoutBox,
  },
  data() {
    return {
      checkStatus: false,
      data2: [
        {
          title: "№3432",
          date: "14.04.3434",
          time: "12.23.43",
          notifications: "23",
        },
        {
          title: "№3432",
          date: "14.04.3434",
          time: "12.23.43",
          notifications: "23",
        },
        {
          title: "№3432",
          date: "14.04.3434",
          time: "12.23.43",
          notifications: "23",
        },
      ],
    };
  },
  setup() {
    const router = useRouter();
    const store = new Storage();
    const { getAppealsList } = useAppealsStore();
    const { appealsListResponse } = storeToRefs(useAppealsStore());
    const fetchAppealsHandler = async () => {
      await store.create();
      const storeValue = await store.get("token");
      const token = JSON.parse(storeValue).token;
      await getAppealsList(token).then(async () => {
        await store.create();

        console.log("getAppealsList", appealsListResponse.value);
      });
    };
    onIonViewDidEnter(() => {
      fetchAppealsHandler();
    });

    return {
      router,
      paperPlaneOutline,
      chevronForwardOutline,
      appealsListResponse,
      /* fetchAppealsHandler, */
    };
  },
});
</script>

<style scoped>
.footer-item {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.item {
  width: 100%;
}
.item-header {
  display: flex;
  justify-content: space-between;
}

.icon-start {
  width: 32px;
  height: 32px;
  margin-right: 15px;
}
.icon-end {
  width: 24px;
  height: 24px;
  margin-left: 0px;
  color: #0378b4;
}
.text {
  margin-left: 0;
}
.time {
  margin-right: 20px;
}
.item-header .title {
  margin-bottom: 0;
}

ion-spinner {
  top: -20px;
}
.loading {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>